@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;700&family=Roboto:wght@400;500;700&display=swap');

/* Koppee Theme Variables */
:root {
  --primary: #DA9F5B;
  --secondary: #33211D;
  --light: #FFFBF2;
  --dark: #111111;
  --gray: #6c757d;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #555555;
  background-color: var(--light);
}

/* Navbar Styles */
.navbar {
  background: rgba(51, 33, 29, 0.9) !important;
  backdrop-filter: blur(10px);
  transition: all 0.3s;
}

.navbar-brand h1 {
  color: white !important;
  font-weight: 700;
  letter-spacing: 2px;
}

.nav-link {
  color: white !important;
  font-weight: 500;
  transition: color 0.3s;
}

.nav-link:hover {
  color: var(--primary) !important;
}

/* Hero Section */
.hero-section {
  background: linear-gradient(rgba(51, 33, 29, 0.7), rgba(51, 33, 29, 0.7)), url('https://images.unsplash.com/photo-1447933601403-0c6688de566e?ixlib=rb-4.0.3');
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
}

.hero-title {
  font-size: 4rem;
  font-weight: 700;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.5);
}

.hero-subtitle {
  font-size: 1.5rem;
  color: var(--primary);
  font-weight: 500;
  margin-bottom: 0.5rem;
}

/* Section Styles */
.section {
  padding: 5rem 0;
}

.section-title {
  text-align: center;
  margin-bottom: 3rem;
}

.section-title h4 {
  color: var(--primary);
  text-transform: uppercase;
  letter-spacing: 5px;
  font-weight: 500;
  margin-bottom: 1rem;
}

.section-title h1 {
  font-size: 2.5rem;
  color: var(--secondary);
  font-weight: 700;
}

/* Button Styles */
.btn-primary {
  background-color: var(--primary);
  border-color: var(--primary);
  color: white;
  font-weight: 600;
  padding: 12px 30px;
  border-radius: 25px;
  transition: all 0.3s;
  text-decoration: none;
  display: inline-block;
}

.btn-primary:hover {
  background-color: #c8934f;
  border-color: #c8934f;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(218, 159, 91, 0.3);
}

.btn-secondary {
  background-color: var(--secondary);
  border-color: var(--secondary);
  color: white;
  font-weight: 600;
  padding: 12px 30px;
  border-radius: 25px;
  transition: all 0.3s;
  text-decoration: none;
  display: inline-block;
}

.btn-secondary:hover {
  background-color: #2a1b18;
  transform: translateY(-2px);
}

/* Card Styles */
.service-card {
  background: white;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  margin-bottom: 2rem;
}

.service-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(0,0,0,0.15);
}

.service-icon {
  color: var(--primary);
  font-size: 2rem;
  margin-right: 1rem;
}

/* Menu Styles */
.menu-item {
  background: white;
  border-radius: 15px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  box-shadow: 0 5px 20px rgba(0,0,0,0.1);
  transition: transform 0.3s;
}

.menu-item:hover {
  transform: translateY(-5px);
}

.menu-price {
  color: var(--primary);
  font-weight: 700;
  font-size: 1.2rem;
}

.menu-image {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
}

/* Reservation Section */
.reservation-section {
  background: linear-gradient(rgba(51, 33, 29, 0.8), rgba(51, 33, 29, 0.8)), url('https://images.unsplash.com/photo-1559925393-8be0ec4767c8?ixlib=rb-4.0.3');
  background-size: cover;
  background-position: center;
  color: white;
}

.reservation-form {
  background: rgba(51, 33, 29, 0.9);
  border-radius: 15px;
  padding: 2rem;
}

.form-control {
  background: transparent;
  border: 2px solid var(--primary);
  color: white;
  padding: 12px 20px;
  border-radius: 25px;
  margin-bottom: 1rem;
}

.form-control::placeholder {
  color: rgba(255,255,255,0.7);
}

.form-control:focus {
  background: transparent;
  border-color: var(--primary);
  color: white;
  box-shadow: 0 0 0 0.2rem rgba(218, 159, 91, 0.25);
}

/* Footer */
.footer {
  background: var(--secondary);
  color: white;
  padding: 3rem 0 1rem;
}

.footer h4 {
  color: white;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 1.5rem;
}

.footer a {
  color: rgba(255,255,255,0.8);
  text-decoration: none;
  transition: color 0.3s;
}

.footer a:hover {
  color: var(--primary);
}

/* Responsive */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.2rem;
  }
  
  .section-title h1 {
    font-size: 2rem;
  }
}

/* Language Selector */
.language-selector {
  background: transparent;
  border: 2px solid var(--primary);
  color: white;
  padding: 8px 15px;
  border-radius: 20px;
  font-weight: 500;
}

.language-selector option {
  background: var(--secondary);
  color: white;
}

/* Utility Classes */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.col {
  flex: 1;
  padding: 0 15px;
}

.col-md-6 {
  flex: 0 0 50%;
  max-width: 50%;
  padding: 0 15px;
}

.col-lg-4 {
  flex: 0 0 33.333333%;
  max-width: 33.333333%;
  padding: 0 15px;
}

@media (max-width: 768px) {
  .col-md-6, .col-lg-4 {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

.text-center {
  text-align: center;
}

.mb-4 {
  margin-bottom: 2rem;
}

.mb-3 {
  margin-bottom: 1.5rem;
}

.mb-2 {
  margin-bottom: 1rem;
}

.mt-4 {
  margin-top: 2rem;
}